const getShareGoods = (params) => {
    console.log(params)
    return new Promise((resolve, reject) => {
        http.get('xwj-sys-user/share/goodsDetail', params).then(result => {
            resolve(result);
        }).catch(error => {
            reject(error);
        })
    });
}
